import IReportRow from "./report-row";

export function ratingText(row: IReportRow): string {
    switch (row.rating) {
        case 6: return "AAA";

        case 5.1: return "AA+";
        case 5.0: return "AA";
        case 4.9: return "AA-";

        case 4.1: return "A+";
        case 4.0: return "A";
        case 3.9: return "A-";

        case 3.1: return "BBB+";
        case 3.0: return "BBB";
        case 2.9: return "BBB-";

        case 2.1: return "BB+";
        case 2.0: return "BB";
        case 1.9: return "BB-";

        case 1.1: return "B+";
        case 1.0: return "B";
        case 0.9: return "B-";
    }
    return "–";
}

export function couponHintText(row: IReportRow) {
    switch (row.couponType) {
        case 1: return " ↓";
        case 2: return "**";
        case 3: return "оф";
    }
    return "";
}

export function couponValueText(row: IReportRow) {
    const { couponValues } = row;
    if (!couponValues.length) {
        return "–";
    }
    return couponValues[0];
}

const TSV_LEN_TEXT = 12;
const TSV_LEN_TAGS = 7;
const TSV_LEN_LIST_LEVEL = 4;
const TSV_LEN_RATING = 6;
const TSV_LEN_DATE = 10;
const TSV_LEN_NUM = 8;

export function toTsvHeader() {
    const cells = [
        pad("isin", TSV_LEN_TEXT),
        pad("name", TSV_LEN_TEXT),
        pad("", TSV_LEN_TAGS),
        pad("list", TSV_LEN_LIST_LEVEL, true),
        pad("rating", TSV_LEN_RATING),
        pad("mat_date", TSV_LEN_DATE),
        "",
        pad("wa_price", TSV_LEN_NUM, true),
        "",
        pad("coupon", TSV_LEN_NUM, true),
        pad("yield", TSV_LEN_NUM, true)
    ];
    return cells.join("\t");
}

export function toTsvLine(row: IReportRow) {
    const cells = [
        row.isin,
        pad(row.name, TSV_LEN_TEXT),
        tags(),
        pad(String(row.listLevel), TSV_LEN_LIST_LEVEL, true),
        pad(ratingText(row), TSV_LEN_RATING),
        row.maturityDate,
        row.hasFutureAmort ? "A" : "",
        numeric(row.waprice),
        couponHintText(row),
        numeric(couponValueText(row)),
        numeric(row.closeYield)
    ];

    function tags(): string {
        const list = [ ];

        if (row.isQualOnly) {
            list.push("кв");
        } else if (row.inABI) {
            list.push("*");
        } else if (row.inCBHY) {
            list.push("вд");
        }

        if (row.isHighRisk) {
            list.push("пир");
        }

        return pad(list.join(", "), TSV_LEN_TAGS);
    }

    function numeric(value: number | "–"): string {
        return pad(String(value || ""), TSV_LEN_NUM, true);
    }

    return cells.join("\t");
}

function pad(text: string, len: number, left = false): string {
    while (text.length < len) {
        if (left) {
            text = " " + text;
        } else {
            text = text + " ";
        }
    }
    return text;
}
